<template>
  <Suspense>
    <div v-if="state.is_available_device">
      <template v-if="widgetOnly">
        <div
          class="bg-white p-4 overflow-y-auto rounded-lg"
          :style="{ minHeight: '450px', maxHeight: '600px' }"
        >
          <div id="buybackWidget">
            <WidgetPreview
              :loading="false"
              :apiKey="widgetOptions.apiKey"
              :tenantId="widgetOptions.tenantId"
              :available="state.is_available_device"
              :useIframe="false"
            />
          </div>
        </div>
      </template>
      <template v-else>
        <Card class="shadow-lg h-full">
          <template #header>
            <div class="flex">
              <div class="flex-grow">
                <div class="flex items-center flex-wrap">
                  <p class="font-medium text-teal-04 text-lg mr-4">{{ orderTitle }}</p>
                </div>
              </div>
            </div>
          </template>
          <div class="overflow-y-auto">
            <div id="buybackWidget">
              <WidgetPreview
                :loading="false"
                :apiKey="widgetOptions.apiKey"
                :tenantId="widgetOptions.tenantId"
                :available="state.is_available_device"
                :useIframe="false"
              />
            </div>
          </div>
        </Card>
      </template>
    </div>
    <div v-else>
      <Card class="shadow-lg h-full">
        <template #header>
          <div class="flex">
            <div class="flex-grow">
              <div class="flex items-center flex-wrap">
                <p class="font-semibold mr-4">{{ orderTitle }}</p>
              </div>
            </div>
          </div>
        </template>
        <div class="overflow-y-auto">
          <WidgetUnvailable />
        </div>
      </Card>
    </div>
  </Suspense>
</template>

<script>
import { defineComponent } from 'vue'
import { useDevice } from 'composable/dashboard'
import { useUser } from 'composable/user'

import Card from 'bbUi/components/card/card.vue'
import WidgetUnvailable from 'components/Widget/Unavailable/index.vue'
import WidgetPreview from 'components/Widget/Preview/index.vue'

export default defineComponent({
  components: {
    Card,
    WidgetUnvailable,
    WidgetPreview
  },
  props: {
    statusId: {
      type: Number,
      default: null
    },
    orderTitle: {
      type: String,
      default: ''
    },
    buyback_type: {
      type: [String, Number],
      default: 2
    },
    widgetOnly: {
      type: Boolean,
      default: false
    }
  },
  async setup() {
    const { state, actionConditionDeviceWidgetAvailability } = useDevice()
    const { widgetOptions } = useUser()
  
    await actionConditionDeviceWidgetAvailability()

    return {
      state,
      widgetOptions
    }
  }
})
</script>
